<template>
  <div class="layout-container">
    <header class="fixed-header">
      <header-component />
    </header>

    <main class="main-content">
      <router-view />
    </main>

    <hr class="footer-divider" />
    
    <footer class="footer">
      <footer-component />
    </footer>

    <el-backtop :right="10" :bottom="70" />
  </div>
</template>

<script>
import HeaderComponent from './Header.vue';
import FooterComponent from './Footer.vue';

export default {
  name: "LayoutPage",
  components: {
    HeaderComponent,
    FooterComponent
  },
  mounted() {
    this.adjustMainContentMargin();
    window.addEventListener('resize', this.adjustMainContentMargin);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.adjustMainContentMargin);
  },
  methods: {
    adjustMainContentMargin() {
      const header = this.$el.querySelector('.fixed-header');
      if (header) {
        // Đặt margin-top của main-content bằng chiều cao thực tế của header
        const headerHeight = header.offsetHeight;
        this.$el.querySelector('.main-content').style.marginTop = `${headerHeight}px`;
      }
    }
  }
};
</script>

<style scoped>
.layout-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background-color: rgb(2, 2, 2);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.main-content {
  flex: 1; /* Cho phép nội dung chính giãn nở để lấp đầy khoảng trống */
  padding: 20px 15px; /* Thêm padding ngang và dọc cho nội dung */
}

/* Thêm padding cho nội dung trên màn hình lớn để tránh sát lề quá */
@media (min-width: 768px) {
  .main-content {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.footer-divider {
  border: 0;
  height: 1px;
  background-color: #333;
  margin: 20px 0;
}

.footer {
  padding: 20px 0;
}
</style>
